/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

.leaflet-container {
    height: 800px;
    margin: 5px;
}
.container {
    max-width: 1200px;
    margin: 20px auto;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 0 20px grey;
}
body {
    color:#000;
    background-color: #fff;
}
.search {
    background-color: #6E8FDB;
    text-align: center;
    padding: 20px;
    border-radius: 10px 10px 0 0;
}
.search .title {
    font-size: 50px;
    display: inline-block;
    margin-right: 30px; 
    color: #fff;
}
.search form {
    position: relative;
    bottom:9px;
    display: inline-block;

}
.search input {
    padding: 2px;
    font-size: 25px;
    /* border: 5px solid */
}
.search button {
    background-color: #6E8FDB;
    color: #fff;
    border: 1px solid #fff;
    padding: 9px;
    position: relative;
    bottom: 4px;
}

.result {
    text-align: center;
    color:#263B90;
}
.result .leaflet-container {
    width: 400px;
    height: 400px;
    margin: 0 auto;
}
.result .name {
    font-size: 50px;
    display: inline-block;
    color:#F69000;
}

.result .clouds {
    font-size: 50px;
    display: inline-block;
}

.result .temperature {
    width: 200px;
    margin: 0 auto;
    display: flex;
}
.result .average {
    background-color: blue;
    font-size: 25px;
    padding: 10px; 
}
.result .min {
    background-color: orange;
    font-size: 15px;
    padding: 5px; 
}
.result .max {
    background-color: aqua;
    font-size: 15px;
    padding: 5px; 
}
.result .data {
    width:300px;
    margin: 0 auto;
}
.result .suntime {
    font-size: 20px;
    display: flex;
    align-items: center;
}
.graph {
    margin: 0 auto;
}
